.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
    align-items: center;
}
.dropdown{
    display: none;
}

.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: #171717;
    font-size: 36px;
    font-weight: 600;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    font-size: 20px;
    color: #626262;
}

.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #ff4141;
}
.nav-logo-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}

.nav-logo-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    background: white;
    cursor: pointer;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}
.nav-logo-cart button:active{
    background-color: #87CEEB;
}

.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;

}

@media(max-width:1250px){
    .navbar{
        padding: 12px 50px;
    }
    .nav-logo img{
        width: 40px;
    }
    .nav-logo p{
        font-size: 25px;
    }
    .nav-menu{
        gap: 30px;
        font-size: 16px ;
    }
    .nav-logo-cart{
        gap: 30px;
    }
    .nav-logo-cart button{
        width: 120px;
        height: 45px;
        font-size: 16px;
    }
    .nav-cart-count{
        margin-left: -40px;
        font-size: 12px;
    }

}
@media(max-width:1024px)
{
    .navbar{
         padding: 12px 30px;
    }
    .nav-menu{
        gap: 25px;
        font-size: 14px;
    }
    .nav-logo-cart button{
        width: 70px;
        height: 35px;
        font-size: 14px;
    }
    .nav-logo-cart img{
        width: 30px;
    }
    .nav-cart-count{
        width: 18px;
        height: 18px;
    }
}
@media (max-width:800px) {

    .navbar{
        padding: 10px 0px;
    }
    .dropdown{
        display: block;
        width: 30px;
        rotate: 260deg;
        transition: 0.5s
        /* height: vh; */
    }
    .nav-menu{
        display: none;
        height: 80px;
        width: 100%;
        position: absolute;
        background-color: white;
        justify-content: center;
        top: 50px;

    }
    .nav-menu-visible{
        display: flex;
    }
    .dropdown.open{
        transform: rotate(90deg);
    }
    
}

@media (max-width:500px) {
    .navbar{
        width: 8px 0px;
        gap: 0;
    }
    .nav-logo{
        transform: scale(0.9);
    }
    .nav-menu{
        height: 70px;
         top: 50px
    }
    .nav-logo-cart{
        transform: scale();
    }
    
}