.breadcrum{
    display: flex;
    align-items: center;
    gap: 8px;
    color: #5e5e5e;
    font-size: 16px;
    font-weight: 600px;
    margin: 60px 170px;
    text-transform: capitalize;
}
@media(max-width:1280px)
{
    .breadcrum{
        margin: 30px 50px;
        font-size: 14px;
    }
}
@media(max-width:800px)
{
    .breadcrum{
        margin: 30px 10px;
        font-size: 12px;
        /* color: red; */
    }
}
@media(max-width:450px)
{
    .breadcrum{
        /* margin: 30px 10px; */

        display: none;
        grid-column: 1fr 1fr;
        font-size: 10px;
        gap: 2px;
        
    }
}